import React, { Component, Suspense } from 'react';
import './slider.scss';

import MercadoPago from '../img/ecommerce.png';
import DinosBurger from '../img/dinos.png';
import CartaRestaurant from '../img/carta_restaurante.png';

const Project = React.lazy(() => import('./Project'));

export default class Proyects extends Component {
    render() {
        return (
            <div className="uk-section uk-section-default uk-text-center uk-margin-medium-top uk-margin-bottom">
                <div className="uk-container uk-container-xlarge">


                    <h2 className="uk-margin-small-top">PROYECTOS</h2>
                    <div data-uk-slideshow="autoplay: true; autoplay-interval: 5000; ratio: 2:2" className="uk-box-shadow-xlarge">

                        <div className="uk-position-relative uk-visible-toggle uk-light " data-tabindex="-1" data-uk-slideshow="animation: push">
                            <ul className="uk-slideshow-items border-radius " >
                                <Suspense fallback={

                                    <div className="uk-position-center uk-position-small uk-text-center uk-light overlay-shaddw" >
                                        <span data-uk-spinner="ratio: 8" className="custom-color"></span>
                                    </div>}>

                                    <Project
                                        img={DinosBurger}
                                        name="Punto de venta a la medida"
                                        description="Software punto de venta hecho con C# y SQL Server para negocio de alimentos preparados."
                                    />

                                    <Project
                                        img={MercadoPago}
                                        name="Mercado Pago E-commerce"
                                        description="E-commerce de ejemplo para realizar integración de Mercado Pago mediante API."
                                        urlSite="https://nicolasgl-mp-ecommerce-nodejs.herokuapp.com"
                                    />

                                    <Project
                                        img={CartaRestaurant}
                                        name="Carta Digital"
                                        description="Maquetación de ejemplo de menú digital para restaurante"
                                        urlSite="https://carta-restaurante.netlify.app"
                                    />
                                </Suspense>
                            </ul>

                            <i className="uk-position-center-left uk-position-small uk-hidden-hover" href="#" data-uk-slidenav-previous data-uk-slideshow-item="previous"></i>
                            <i className="uk-position-center-right uk-position-small uk-hidden-hover" href="#" data-uk-slidenav-next data-uk-slideshow-item="next"></i>

                            <div className="uk-position-bottom-center uk-position-small">
                                <ul className="uk-slideshow-nav uk-dotnav uk-dotnav-contrast"></ul>
                            </div>
                        </div>
                    </div>
                </div>
                <span id="Contact"></span>
            </div>
        )
    }
}

